import React from 'react';

import {  PanelHeader,  Avatar, Div, HeaderButton, List, Cell, Snackbar, Group, InfoRow, Progress, Tabs, TabsItem} from '@vkontakte/vkui';
import '@vkontakte/vkui/dist/vkui.css';





const blueBackground = {
  backgroundColor: 'var(--accent)'
};

class Questions extends React.Component {
constructor(props){
super(props)

this.state ={
activeTab:'pr'


}
}
  
	render(){
		return(
			<Div>
			     <Tabs theme="light">
                <TabsItem
                 onClick={() => this.setState({ activeTab4: 'pr' })}
                  selected={this.state.activeTab === 'pr'} 
                >
                  Задания
                </TabsItem>
                <TabsItem
                     onClick={() => this.setState({ activeTab: 'di' })}
                  selected={this.state.activeTab4 === 'di'}
                  
                >
                  Рейтинг
                </TabsItem>
              </Tabs>
          <Div> 
         <List>

     {(this.props.quests[0]).toString() === "object Object" ? 
     this.props.quests.map((name, i) => <Cell before={<Avatar src={this.props.quests[i].value.img}/>} key={i} style={{"background": "linear-gradient(to right, #8B04A8, #E9003A)"}}><InfoRow title={this.props.quests[i].value.name}> <Progress  value={this.props.quests[i].value.num} style={{"color": "green"}}/></InfoRow></Cell>):
     this.props.quests.map((name, i) => <Cell before={<Avatar src={JSON.parse(this.props.quests[i].value).img}/>} key={i} style={{"background": "linear-gradient(to right, #8B04A8, #E9003A)"}}><InfoRow title={JSON.parse(this.props.quests[i].value).name}> <Progress  value={JSON.parse(this.props.quests[i].value).num} style={{"color": "green"}}/></InfoRow></Cell>)}
  </List>   
 
          </Div>
            
              </Div>
			);
	}
}
export default Questions;
