import React from 'react';
import connect from '@vkontakte/vkui-connect';
import {  View, ScreenSpinner, Epic, Button,PanelHeader, Panel, Tabbar, TabbarItem, Avatar,Alert, Div, HeaderButton, Cell, Snackbar, Gallery, Group} from '@vkontakte/vkui';
import '@vkontakte/vkui/dist/vkui.css';
import Profile from './panels/Profile';

import Modal from './panels/Modal';
import Achievment from './panels/Achievment';
import MapContainer from './panels/MapContainer';
import End from './panels/End'
import Poll from './panels/Poll';
import Begin from './panels/Begin'
import Questions from './panels/Questions';

import Icon28MoreHorizontal from '@vkontakte/icons/dist/28/more_horizontal';
import Icon28Place from '@vkontakte/icons/dist/28/place';
import Icon28User from '@vkontakte/icons/dist/28/user';
import Icon24Back from '@vkontakte/icons/dist/24/back';
import Icon16Done from '@vkontakte/icons/dist/16/done';


const orangeBackground = {
  backgroundImage: 'linear-gradient(135deg, #ffb73d, #ffa000)'
};

const blueBackground = {
  backgroundColor: 'var(--accent)'
};
class App extends React.Component {
  constructor (props) {
    super(props);
  


 

this.so = <p style={{color: 'red'}}>Попробуйте еще раз</p>;
    this.num = 2;
    
    this.state = {
      activeStory: null,
      activePanel: "map",
      quests:null,
      header: true,
      snackbar: null,
      popout: null,
      selectedPlace:null,
      fetchedUser: null,
      access_token: null,
      geoData: null,
      errorGeo:null,
      Poll:null,
      showEnd:false
    
      
    };
    this.onStoryChange = this.onStoryChange.bind(this);
    this.onToChange
     = this.onToChange.bind(this);

    this.openDefault = this.openDefault.bind(this);
    this.closePopout = this.closePopout.bind(this);
  
  }


  componentDidMount() {   //приходят события vk connect
    connect.subscribe((e) => {
      switch (e.detail.type) {
        case 'VKWebAppGetUserInfoResult':
         
           this.setState({fetchedUser: e.detail.data});
           this.setState({activeStory: "profile"})
           console.log(e.detail.data.id);
        break;

          case 'VKWebAppGeodataResult':
          console.log(e.detail.data.available);
          if(e.detail.data.available === true){
          this.setState({geoData: e.detail.data})
        }else{
 this.setState({geoData: null})

        }
          break;

          case 'VKWebAppGeodataFailed':
          this.openDefault();
          break;

          case 'VKWebAppAccessTokenReceived':
             this.setState({ access_token: e.detail.data.access_token});
             //connect.send("VKWebAppCallAPIMethod", {"method": "storage.set", "request_id": "g", "params": {"key": "one_1Poll","value": "", "v": "5.101", "access_token": e.detail.data.access_token}});
          break;

          case 'client_error':
                      
          break;

          case 'VKWebAppCallAPIMethodResult':
            switch(e.detail.data.request_id){
    

             case "getAll":
            // let arr = JSON.parse(e.detail.data.response[1].value);
            console.log((e.detail.data.response[0].value).toString());
              this.setState({quests:e.detail.data.response});
             
               this.setState({ activeStory: 'questions', popout:null });
               
             break;

             case 'getAllQuests':
             
              connect.send("VKWebAppCallAPIMethod", {"method": "storage.get", "request_id": "getAll", "params": {"keys":(e.detail.data.response).join(","),"user_id": this.state.fetchedUser.id, "v":"5.101", "access_token": '1391f8d11391f8d11391f8d11e13fce3f9113911391f8d14e1ecdb0e32793de2eeffe66'}});
                console.log(e.detail.data.response);
                
             break;

    

             case "g":
      console.log(e.detail.data);
             break;

          
          }
          break;
        
        default:
        this.openDefault();
        console.log(e.detail)  ;
      }
    });
    connect.send('VKWebAppGetUserInfo', {});
    connect.send("VKWebAppGetAuthToken", {"app_id":7150376, "scope": "friends"});
      connect.send("VKWebAppGetGeodata", {});


   
  }


     openDefault () {
    this.setState({ popout:
      <Alert
        actions={[{
          title: 'Отмена',
          autoclose: true,
          style: 'cancel'
        }, {
          title: 'Ок',
          autoclose: true,
        }]}
        onClose={this.closePopout}
      >
        
        <p>Для стабильной включите геолокацию</p>
      </Alert>
    });
  }

   closePopout () {
    this.setState({ popout: null,activeStory:'profile' });
  }
  onClick =(e) => {
    connect.send("VKWebAppGetGeodata", {});
    this.setState({ popout: <ScreenSpinner /> });
  
    setTimeout(() => { this.setState({ popout: null }) }, 2000);  
       setTimeout(()=>{ this.setState({ activeStory:'map'})}, 1500)
  }
  
 
 theStory=(e)=>{this.setState({activePanel:"map"})} 

theEnd=(e)=>{
let arr = {
  name:this.state.selectedPlace.name,
  img:this.state.selectedPlace.img,
  num: 100};
  console.log(arr);
        connect.send("VKWebAppCallAPIMethod", {"method": "storage.set", "request_id": "theEnd", "params": 
        {"key": this.state.selectedPlace.id_marker, "value":arr ,"user_id": this.state.fetchedUser.id, "v":"5.101", "access_token": '1391f8d11391f8d11391f8d11e13fce3f9113911391f8d14e1ecdb0e32793de2eeffe66'}});
this.setState({activePanel:'modal'});
      }
 

     getAns=(e)=>{
      console.log("qwwwwwwwwwwwwwwwwwww");
this.setState({showEnd:true});      
     }


  GetVar = (e)=>{
    this.setState({popout: <ScreenSpinner />});
    connect.send("VKWebAppCallAPIMethod", {"method": "storage.getKeys", "request_id": "getAllQuests", "params": 
        {"user_id": this.state.fetchedUser.id, "v":"5.101", "access_token": '1391f8d11391f8d11391f8d11e13fce3f9113911391f8d14e1ecdb0e32793de2eeffe66'}});
  }
  checkGeo = (a, e) =>{
    console.log("lf");  
    this.setState({popout:<ScreenSpinner />});
  connect.send("VKWebAppGetGeodata", {});
    if((this.state.geoData.lat).toFixed(this.num) === (a.position.lat).toFixed(this.num) && 
      (this.state.geoData.long).toFixed(this.num) === (a.position.lng).toFixed(this.num)){
      this.setState({errorGeo: null});
  let arr = {
    name:a.name,
    img:a.img,
    num: 50};
      connect.send("VKWebAppCallAPIMethod", {"method": "storage.set", "request_id": "setQuests", "params": 
        {"key": a.id_marker , "value": arr,"user_id": this.state.fetchedUser.id, "v":"5.101", "access_token": '1391f8d11391f8d11391f8d11e13fce3f9113911391f8d14e1ecdb0e32793de2eeffe66'}});
    this.setState({activePanel:'questions', popout:null})
      this.setState({selectedPlace: a});
     
       
       
    }
    else{
   this.setState({errorGeo:this.so , popout:null});
    
    }
  }


  onStoryChange (e) {
    this.setState({ activeStory: e.currentTarget.dataset.story });
  }

  onToChange =(e)=> {
     this.setState({ activePanel: e.currentTarget.dataset.to});

  }

  render =()=> {
    return (
      <Epic activeStory={this.state.activeStory} tabbar={
        <Tabbar>
          <TabbarItem
            onClick={this.onStoryChange}
            selected={this.state.activeStory === 'profile'}
            data-story="profile"
          ><Icon28User /></TabbarItem>
          <TabbarItem
            onClick={this.onClick}
            selected={this.state.activeStory === 'map'}
            data-story="map"
           
          ><Icon28Place/></TabbarItem>
          <TabbarItem
            onClick={this.GetVar}
            selected={this.state.activeStory === 'questions'}
            data-story="questions"
           
           
          ><Icon28MoreHorizontal /></TabbarItem>
  
        </Tabbar>
      }>

      
        <View id="profile" popout={this.state.popout} activePanel="profile">
          <Panel id="profile">
            <PanelHeader>Профиль</PanelHeader>
        
           <Profile fetchedUser={this.state.fetchedUser}/>
          </Panel>
        </View>

       
        <View id="map" popout={this.state.popout} header={false} activePanel={this.state.activePanel}>
          <Panel id="map">
           
  {this.state.geoData === null ? <p>Попробуйте включить геолокацию и пезезайти.</p>:
            <MapContainer geo={this.state.geoData} checkGeo={this.checkGeo} errorGeo={this.state.errorGeo}/>
         }         
          </Panel>
          <Panel id="questions">
          
            <Begin selectedPlace={this.state.selectedPlace} />     
            <Poll selectedPlace={this.state.selectedPlace} getAns={this.getAns} />
         <Div>
{this.state.showEnd && <End theEnd={this.theEnd} selectedPlace={this.state.selectedPlace} />}
         </Div>
          </Panel>
          <Panel id="modal">
            <Modal fetchedUser={this.state.fetchedUser} theStory={this.theStory} />
          </Panel>

        </View>

       

        <View id="questions" popout={this.state.popout} activePanel="quest">
          <Panel id="quest">
            <PanelHeader>Задания</PanelHeader>
          <Questions quests={this.state.quests}/>
          </Panel>

        </View>
 
      </Epic>)
  }
}

export default App;
